<template>
    <v-container >
      <div class="text-center">
          <h2>Examenes de Laboratorio con Info Pendiente</h2>
      </div>
        <v-form>

            <v-row class="ma-0 pa-0" justify="center"  >
                           
              <v-col cols="12" sm="6" md="4 mb-2" class="ma-0 pa-0"  >
                
                <v-card  outlined  class="pt-3 mx-1 px-1 red lighten-3" v-if="ambienteDeModificacion==true" >
                  <v-row class="ma-0 pa-0">
                  <v-col class="ma-0 pa-0" cols="6">
                     <v-switch  dense  v-model="examenesRealizadosLab" :label="examenesLab.muestra" :value="examenesLab.id" class="ma-0 pa-0" color="red darken-4" >  </v-switch>
                  </v-col>
               </v-row>   
                
                  <v-row v-if="verificarExamenRealizado(examenesLab.id)" justify="center" class="ma-0 pa-0">
                    <v-col  cols="6" class="ma-0 pa-0">
                      <v-checkbox
                      class="ma-0 pa-0"  
                      dense
                      v-model="examenesLab.enviado"
                      label="¿Enviado?"
                      color="red darken-4"
                      :value="!enviado"
                      hide-details
                     ></v-checkbox>
                     </v-col>
                     <v-col  cols="6" class="ma-0 pa-0">             
                          <v-text-field v-model="examenesLab.fechaResultado" type="date"  color="red darken-4" label="Entrega del Resultado" dense  outlined background-color="red lighten-4"  ></v-text-field>
                    </v-col>
                  </v-row>                   
                </v-card>
              </v-col>
            </v-row>
            
  <v-row justify="center" class="ma-0 pa-0" v-if="ambienteDeModificacion==true">
      
            <v-col cols="11" md ="4">
                <v-btn class="px-5" block dark color="red darken-4" @click="Modificar(examenesLab)" >Modificar</v-btn>
             </v-col>
              <v-col cols="11" md ="4" >
                <v-btn class="px-5" block dark type="submit" @click="ambienteDeModificacion =false">Cancelar</v-btn>
            </v-col>
     
  </v-row>
  <v-row justify="center" class="ma-0 pa-0">
    <v-alert v-if="mensajeError!==''" dense outlined type="error">
          {{ mensajeError }}</v-alert>
          <v-alert v-if="modificacionHecha" dense class="text-center" type="success">
          Registro Exitoso</v-alert>
  </v-row>
  </v-form>
   
     <v-col  class="mx-auto">
           <tabla-pendiente @DatosParaModificarExamenesPendiente="LlenaDatosParaModificacion" ref="tabla"></tabla-pendiente> 
      </v-col>

    </v-container>
  
</template>
<script>
import tablaPendiente from '../components/TablaLaboratorioPendiente'
import {mapState} from 'vuex'
export default {
    name:'LabClinicoPendiente',
    components:{tablaPendiente},
    data() {
        return {
            modificacionHecha:false,
                mensajeError:'',
          ambienteDeModificacion:false,
          examen:'',        
          //esta variable contiene a todos los que estana ctivos o examenes realizados switch True 
          examenesRealizadosLab:[],
          enviado:true,
          dataTipoMuestraLab:[],
          dataMuestraLab:[],
          //esta variable contiene TODO LOS DATOS de los examenes que seran registrados omodificados
            examenesLab:[],
        }
    }   ,
    computed:{
          ...mapState(['token'])
    },
    methods: {

      FechaActual(){
          const hoy = new Date();
          var año=hoy.getFullYear()
          var mes=(hoy.getMonth() + 1)
              mes=(mes<10)?('0'+mes):mes;
          var dia=hoy.getDate()
              dia=(dia<10)?('0'+dia):dia;
       return año+'-'+mes+'-'+dia;
      },
      convertidorFecha(str){
        

          if(str==null){
            return 'Sin Data'
          }else{
            const f = new Date(str);
              var año=f.getFullYear()
              var mes=(f.getMonth() + 1)
                  mes=(mes<10)?('0'+mes):mes;
              var dia=f.getDate()+1
                  dia=(dia<10)?('0'+dia):dia;
          return año+'-'+mes+'-'+dia;
          }
     },

      LlenaDatosParaModificacion(d){
         
        this.ambienteDeModificacion=true;//esta para eliminar
        //Vaciamos par modificar otro dato
                this.examenesRealizadosLab=[];
            if (!(d.fechaAtencion==null)){
                   //agregando a los examenes realizados si es que tien fecha de atendido
                      this.examenesRealizadosLab[0]=d.idMuestraLab
              } 
               
            this.examenesLab={
                  idExamen:d.idExamen,
                  id:d.idMuestraLab,
                  muestra:d.examen,
                  enviado:d.estadoEnvio,
                  atendido:d.fechaAtencion==null? false:true ,
                  fechaResultado:d.fechaEntregaResultado==null? '':this.convertidorFecha(d.fechaEntregaResultado)
                  }
          },
       verificarExamenRealizado(ele){
        
            var existe=this.examenesRealizadosLab.includes(ele);
              //como no ah sido atendido no le agregamos datos al card
            if(!existe){
                   this.examenesLab.atendido=false
                   //si no esta atendido la fecha de resultado sera asignado vacio
                   this.examenesLab.fechaResultado=''}
                  else{
                      this.examenesLab.atendido=true
                   }

      return existe;
      },
      Modificar(dat){
           var headers = {
        "Content-Type": "application/json",
        token: this.token,
       };

          this.axios.post("/modificarUnSoloExamLaboratorio",{
                      idExamen:dat.idExamen,
                      atendido:dat.atendido? this.FechaActual():null ,
                      fechaResultado:dat.fechaResultado,
                      enviado:dat.enviado,
                      id:dat.id

          },{headers})
         .then(res=> {
                this.modificacionHecha=true;
                this.$refs.tabla.ListarMuestras();
                this.ambienteDeModificacion =false;  
                this.examenesLab={}
                this.examenesRealizadosLab=[]               
                setTimeout(()=>{
                  this.modificacionHecha=false ;
                },2000)
          })
          .catch(err=>{
              this.mensajeError=err;
              setTimeout(()=>{  this.mensajeError=''},2000)
              console.log(err)})  
      
      }
    }
}
</script>