  <template>
  <div>
    
    <v-simple-table dark dense fixed-header class="my-2" height="450px">
      <template v-slot:default >
        <thead>
          <tr>
            <th
              class="red darken-4 text-center subtitle-1 font-weight-bold white--text"
              style="
                border-top-left-radius: 5px;
                border-bottom-left-radius: 5px;             "
            >
               *
            </th>
            <th
              class="red darken-4 text-center subtitle-1 font-weight-bold white--text"
            >
              Dni
            </th>
            <th
              class="red darken-4 text-center subtitle-1 font-weight-bold white--text"
            >
              Paciente
            </th>
            <th
              class="red darken-4 text-center subtitle-1 font-weight-bold white--text"
            >
              Examen
            </th>
            <th
              class="red darken-4 text-center subtitle-1 font-weight-bold white--text"
            >
              fechaRegistroExamen
            </th>
            <th
              class="red darken-4 text-center subtitle-1 font-weight-bold white--text"
            >
              FechaAtencion
            </th>
             <th
              class="red darken-4 text-center subtitle-1 font-weight-bold white--text"
            >
              EstadoEnvio
            </th>
            <th
              class="red darken-4 text-center subtitle-1 font-weight-bold white--text" >
              fechaResultado
            </th>
            <th class="red darken-4 text-center subtitle-1 font-weight-bold white--text" ></th>
          </tr>
        </thead>
        <tbody>
            <tr  v-for="(item, index) in LaboratorioData" :key="index">
              <td class="text-center">{{ index +1}}</td>
              <td class="text-center">{{ item.dni }}</td>
              <td class="text-center">{{ item.nombres }} {{ item.apellidos }}</td>
              <td class="text-center">{{ item.examen}}</td>
              <td class="text-center" :class="{ 'red':item.fechaRegistroExamen==null}" >{{ convertidorFecha(item.fechaRegistroExamen)}}</td>
              <td class="text-center" :class="{ 'red':item.fechaAtencion==null}" >{{ convertidorFecha(item.fechaAtencion) }}</td>
              <td class="text-center" :class="{ 'red':item.estadoEnvio==false}" >{{item.estadoEnvio==0?'NO':'SI'  }}</td>
              <td class="text-center" :class="{ 'red':item.fechaEntregaResultado==null}">{{ convertidorFecha(item.fechaEntregaResultado)}}</td>
              <td class="text-center">
                <v-btn  @click="editarDataLab(item)"  elevation="3" icon outlined small class="ma-1">        
                  <v-icon>edit</v-icon>
                </v-btn>
                <v-btn
                  elevation="3"
                  icon
                  outlined
                  small
                  class="ma-1"
                  @click="ShowDialogDeletePat(item.examenes, item.nombres +' '+ item.apellidos,item.idExamen )" >
                  <v-icon>delete</v-icon>
                </v-btn>
            </td>
          </tr>
        </tbody>
      </template>
    </v-simple-table>

    <v-dialog v-model="dialogDeleteExamPat" max-width="290" persistent>
      <v-card class="py-2">
        <v-list-item-content class="text-center">
          <div class="font-weight-bold">
            {{ dataDialogDeletePat.message }}
            <span class="text-center red--text text-darken-4 ma-2">
              {{ dataDialogDeletePat.examen }}
            </span>
          </div>
          <div class="font-weight-bold">
            de
            <span class="text-center red--text text-darken-4 ma-2">
              {{ dataDialogDeletePat.nombre }}
            </span>
          </div>
        </v-list-item-content>
        <v-list-item-content>
          <v-layout mx-2 :justify-space-between="true">
            <v-btn
              @click="Eliminar(dataDialogDeletePat.id)"
              rounded
              dark
              color="red darken-4"
              >Si</v-btn
            >
            <v-btn @click="NotShowDialogDeletePat" rounded dark>No</v-btn>
          </v-layout>
        </v-list-item-content>
      </v-card>
    </v-dialog>

  </div>
</template>
<script>
export default {
  name: "TablaPatologiaPendiente",
  data() {
    return {
      dialogDeleteExamPat: false,
       dataDialogDeletePat: {
        message: "Eliminar",
        id: null,
        examen: null,
        nombre: null,
      },
      LaboratorioData: [],
      dataDetalleExam: [],
    };
  },
  mounted() {
    this.ListarMuestras();
  },
  methods: {
    convertidorFecha(str){
      if(str==null){
        return 'Sin Data'
      }else{
        const f = new Date(str);
          var año=f.getFullYear()
          var mes=(f.getMonth() + 1)
              mes=(mes<10)?('0'+mes):mes;
          var dia=f.getDate()
              dia=(dia<10)?('0'+dia):dia;
       return dia+'/'+mes+'/'+año;
      }
    },
    ShowDialogDeletePat(exam, name, id) {
      this.dataDialogDeletePat.examen = exam;
      this.dataDialogDeletePat.nombre = name;
      this.dataDialogDeletePat.id = id;
      this.dialogDeleteExamPat = true;
    },
    NotShowDialogDeletePat() {
      this.dialogDeleteExamPat = false;
      this.dataDialogDeletePat.data = null;
      this.dataDialogDeletePat.id = null;
    },
    Eliminar(id) {
      console.log("Eliminando.." + id);
      this.dialogLaboratorio = false;
    },
    editarDataLab(data){

      this.$emit('DatosParaModificarExamenesPendiente',data);
    },
    ListarMuestras() {
     
       this.LaboratorioData=[];
       this.dataDetalleExam= [];

      this.axios
        .get("/listarExamPendientesLaboratorio")
        .then((res) => {
          // console.log(res.data)
          this.LaboratorioData=res.data;
        })
        .catch((err) => {
          console.log(err);
        });
          },
  },
};
</script>